@import "../../styles/variables/variables";
@import "../../styles/mixins/mixins";
.header {
  margin: 0;
  padding: 0;
  @media screen and (max-width: 767px) {
    margin: 0 16px 0px;
  }
}
.container {
  display: flex;
  justify-content: space-between;
  @media screen and (min-width: 0) and (max-width: 767px) {
    position: relative;
    flex-direction: column;
  }
}

.button {
  display: none;
  @include hover-svg;
  @media screen  and (max-width: 767px) {
    display: block;
    position: absolute;
    top: 0px;
    left: 0px;
    background-color: inherit;
    cursor: pointer;
    border: none;
    padding: 0;
  }
}

.title {
  color: $main-accent;
  font-weight: 700;
  font-size: 30px;
  line-height: 35px;
  margin: 0;
  @media screen and (max-width: 767px) {
    align-self: flex-end;
    margin-bottom: 8px;
  }
}

.place__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-width: 95px;
  @media screen and (max-width: 767px) {
    align-self: flex-end;
  }
}
.place__img {
  width: 18px;
  height: 18px;
  margin-bottom: 5px;
  align-self: center;
}
.place__name {
  color:$main-dark-grey;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  margin: 0;
  @media screen and (max-width: 767px) {
    font-weight: 400;
  }
}